<template>
<section>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h5 class="modal-title">{{ $t("PaySlipListing") }}</h5>
                        <button type="button" class="close" @click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="d-flex justify-content-between align-items-center flex-wrap">
                        </div>
                        <div class="listing">
                        <div class="table-responsive">
                             <table class="table table-bordered dt-responsive nowrap mt-0">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th>{{ $t("Serial#") }}</th>
                                                    <th>{{ $t("Bill#") }}</th>
                                                    <th>{{ $t("Payment Date") }}</th>
                                                    <th style="text-align: center;">{{ $t("Amount") }}</th>
                                                    <th>{{ $t("Status") }}</th>
                                                    <th>{{ $t("Reference number") }}</th>
                                                    <th>{{ $t("Payment mode") }}</th>
                                                    <th>{{ $t("Action") }}</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-for="(transaction, index) in transaction_history" :key="index">
                                                    <td>{{ index+ 1 }}</td>
                                                    <td>{{ transaction.bill_number }}</td>
                                                    <td> {{$options.filters.formatDateTime(transaction.payment_date) }}</td>
                                                    <td style="text-align: right;">
                                                        {{ $n(transaction.bill_record_amount, "currency", $getLocaleByCurrency(CurrencyCode)) }}
                                                    </td>
                                                    <td>{{ transaction.PaymentStatus }}</td>
                                                    <td>{{ transaction.transaction_ref_id }}</td>
                                                    <td>{{ transaction.Payment_Mode}}</td>
                                                    <td><a href="javascript:;" :title="$t('ViewSlip')" class="btn btn-info mr-2" v-on:click="PrintPaySlip(transaction.bill_record_id)" ><em class="fa fa-eye pr-2"></em>{{ $t('ViewSlip') }} </a></td>
                                                </tr>
                                            </tbody>
                                        </table>                            
                                 </div>
                            </div>
                          
                                   
                    </div>
                </div>
            </div>
        </div>
    </div>
    <PrintPaySlip v-if="IsPrint" :BillRecordData="BillRecordData"></PrintPaySlip>
</section>
</template>

<script>
import DataService from '../../../services/DataService';
import PrintPaySlip from './PrintInvoiceSlip.vue';
export default {
    components: { PrintPaySlip},
    props: {
        BillId: {
            type: Number,
            required: true,
        }
    },
    data() {
        return {
            isLoading: false,
            InvoiceHistory: [],
            textCountertlength: 2000,
            comment: '',
            company_logo: '',
            invoicedate: '',
            transaction_history: [],
            BillNumber: '',
            mode: 'cash',
            IsPrint: false,
            BillRecordData: [],
            CurrencyCode: 'PKR'
        };
    },
    created() {
        var vm=this;
        this.FetchData();
        vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    },
    methods: {
         FetchData: async function () {
            debugger
            var vueObj = this;
            var url = `invoiceId=${vueObj.BillId}`;
            await DataService.GetInvoicePaymentDetails(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                if (response.data.length > 0) {
                    vueObj.transaction_history = response.data;
                }
            }         
            });
        },
        Close() { 
            var vm = this;
            vm.$parent.isPaySlip = false;
        },
         PrintPaySlip:async function(InvoiceRecordId) {
           var vueObj = this;
            var url = `invoiceId=${vueObj.BillId}&invoiceRecordId=${InvoiceRecordId}`;
            await DataService.GetInvoicePaymentRecord(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                if (response.data.length > 0) {
                    vueObj.BillRecordData = response.data[0];
                    vueObj.IsPrint=true;
                }
            }         
            });
        }

    },
};
</script>

