<script>
import DataService from '../../../services/DataService';
import PrintPaySlip from './PrintInvoiceSlip.vue';
export default {
    components: {
        PrintPaySlip
    },
    props: {
        BillRecordData: {
            type: Object,
            required: true,
        }
    },
    data() {
        return {
            isLoading: false,
            CompanyLogo: null,
            InvoiceDate: '',
            PaymentMode: 'Cash',
            BillAmount: 0,
            TransactionRefNumber: null,
            BillTo: null,
            Billrecordid:null,
            //CompanyAddress:null,
            Billno: '',
            paymentdate:null,
            paymentdate:'',
            Notes: '',
            PaymentStatus: '',
            CurrencyCode: 'PKR'
        };
    },
    created() {
        var vm=this;
        this.FetchData();
        vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    },
    methods: {

        formatFullAddress(item) {
            return [
            item.user_address_1,
            item.user_address_2,
            item.user_state,
            item.country_name,
            item.user_zipcode
            ]
            .filter(part => part && part.trim() !== '')
            .join(', ');
        },
        FetchData: async function () {
            debugger
            var vm = this;
            var BillRecordData = vm.BillRecordData;
            
            vm.CompanyLogo = BillRecordData.company_logo;
            vm.InvoiceDate = vm.$options.filters.formatDateTime(BillRecordData.payment_date);
            vm.PaymentMode = BillRecordData.Payment_Mode;
            vm.CompanyName = BillRecordData.company_name;
            vm.CompanyAddress = BillRecordData.company_address || 'No Address Provided';
            vm.CompanyZipCode = BillRecordData.company_zipcode || 'No Zip Code Provided';
            vm.CompanyCountry = BillRecordData.company_country || 'No Country Provided';
            vm.BillAmount = BillRecordData.TotalAmountRecived;
            vm.TransactionRefNumber = BillRecordData.Transaction_Reference_Number;
            vm.Billrecordid = BillRecordData.bill_record_id;
            vm.BillTo = BillRecordData.ClientName;
            vm.Billno = BillRecordData.Bill_number;
            vm.address = BillRecordData.user_address_1 && BillRecordData.user_address_1.trim() !== '' ? this.formatFullAddress(BillRecordData) : 'N/A';
            vm.paymentdate = vm.$options.filters.formatDateTime(BillRecordData.payment_date);
            vm.Notes = BillRecordData.Notes;
            vm.TotalAmount = BillRecordData.TotalAmount;
            vm.PreviousPayment = BillRecordData.PreviousPayment;
            vm.PaymentStatus = BillRecordData.PaymentStatus;
        },
        PrintPaySlip(divId) {
            debugger
            const printContents = document.getElementById(divId).innerHTML;
            const iframe = document.createElement('iframe');
            iframe.style.position = 'absolute';
            iframe.style.width = '0px';
            iframe.style.height = '0px';
            iframe.style.border = 'none';
            document.body.appendChild(iframe);
            const doc = iframe.contentWindow.document;
            doc.open();
            doc.write('<html><head><title>Print</title>');
            doc.write('<style>');
            const styles = document.querySelectorAll('style, link[rel="stylesheet"]');
            styles.forEach(style => {
                doc.write(style.outerHTML);
            });
            doc.write('</style></head><body>');
            doc.write(printContents);
            doc.write('</body></html>');
            doc.close();
            iframe.contentWindow.focus();
            iframe.contentWindow.print();
            iframe.remove();
        },
        Close() {
            var vm = this;
            vm.$parent.IsPrint = false;
        }
    },
};
</script>

