<template>
    <section>
        <tg-list :IsShowAction="true" :showCheckBox="false" ModuleName="Billing" :listType="['List']"
            :RenderRowActionMethod="renderActions" SubModuleCode="Billing" IdentityColumn="payment_id" HeaderText="Payments"
            :ListData="PaymentList" :HeaderData="ListHeaders" :callbackfunction="getPaymentList" :HeaderButtons="[]"
            v-on:SortdataButtonClick="sortdata"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :ListDataCallBackFunction="getPaymentList"
            :LegendArray="[]" :SortExp="sortExp" :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:PagerButtonClick="pagerMethod" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:ActionButtonClick="actionButtonClick">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'payment_date'">
                    <template v-if="data.row.payment_date != null">
                        <div>{{ formatDateTime(data.row.payment_date) }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'bill_issue_date'">
                    <template v-if="data.row.bill_issue_date != null">
                        <div>{{ formatDateTime(data.row.bill_issue_date) }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'bill_due_date'">
                    <template v-if="data.row.bill_due_date != null">
                        <div>{{ formatDateTime(data.row.bill_due_date) }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>
                <template v-if="data.column.COLUMN_NAME === 'invoice_amount'">
                    <div>{{ $n(data.row.invoice_amount, 'currency', $getLocaleByCurrency(CurrencyCode)) }}</div>
                </template>

                <template v-if="data.column.COLUMN_NAME === 'paid_amount'">
                    <div>{{ $n(data.row.paid_amount, 'currency', $getLocaleByCurrency(CurrencyCode)) }}</div>
                </template>
            </template>
        </tg-list>
        <InvoicePaySlip v-if="isPaySlip" :BillId="billId"></InvoicePaySlip>
    </section>
</template>

<script>
import DataService from '../../../services/DataService';
import InvoicePaySlip from '../ClientDetails/InvoicePaySlip.vue'
import schema from './schema';

export default {
    components: { InvoicePaySlip },
    data() {
        return {
            isLoading: false,
            PaymentList: [],
            ListHeaders: schema.Headers,
            TotalRecords: 0,
            leftSearchFields: schema.leftsearchSchema,
            listActions: schema.listActions,
            sortExp: '',
            sortBy: '', 
            pageSize: 10,
            pageNumber: 1,
            isPaySlip: false,
            billId: '',
            CurrencyCode: 'PKR'
        }
    },
    created() {
        var vm=this;
        this.getPaymentList();
        vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');
    },
    methods: {
        async getPaymentList(searchprms = "") {
            debugger;
            var vm=this;
            // let params = {
            //     sortBy: this.sortBy,
            //     sortExp: this.sortExp,
            //     pageNumber: this.pageNumber,
            //     pageSize: this.pageSize,
            //     ...searchprms && { searchprms }
            // };
            debugger
            var params = `sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&pageNumber=${vm.pageNumber}&pageSize=${vm.pageSize}&${searchprms}`;
            await DataService.getPaymentsListing(params).then(res => {
                if (res && res.data && res.data.status === "Success") {
                    this.PaymentList = JSON.parse(res.data.result);
                    this.TotalRecords = this.PaymentList.length > 0 ? this.PaymentList[0].totalRecords : 0;
                    
                }
            });
        },
        pagerMethod(x) {
            debugger;
            this.pageNumber = x.PageNumber;
            this.pageSize = x.PageSize;
            this.getPaymentList();
        },
        actionButtonClick(callbackfunction, item) {
            debugger;
            switch (callbackfunction) {
                case 'ViewPlanDetails':
                    this.clickPaySlip(item);
                    break;
            }
        },
        clickPaySlip(item) {
            debugger;
            this.isPaySlip = true;
            this.billId = item.Bill_id;
        },
        renderActions(row) {
            let rtnActionArray = [];
            this.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                action.isVisible = action.title === 'View';
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        leftCommonSearch(x) {
            debugger;
            this.getPaymentList(x.searchConditionJson);
        },
        formatDateTime(value) {
            if (value) {
                return new Date(value).toLocaleString();
            }
        },
        sortdata: function (sortBy) {
            this.sortBy = sortBy;
            var sortExp = 'ASC';
            if (this.sortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.sortExp = sortExp;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.PageNumber = 1;
            this.getPaymentList();
        },

    }
}
</script>
